import React, { useContext, useEffect, useState } from 'react'
import Context from '../Context';
import { useHistory, useParams } from 'react-router-dom';
import axios from 'axios';
import './NVBH.css'
// import NVBHKHData from '../Component/NVBH/NVBHKHData';
import NVBHData from '../Component/NVBH/NVBHDATA';
export default function NVBH(props) {
  const [ToDo, DisPatch] = useContext(Context);
  const [isSearchDisable, setisSearchDisable] = useState(true);
  const [Search, setSearch] = useState();
      //INPUT Search CHANGE

  const history = useHistory();
  const { manv } = useParams();
  

  useEffect(() => {
    if (ToDo.isLogin === false) {
      history.replace("/");
    }
    else {
      axios.get(`http://localhost:8080/NV/GetInfoNV/${manv}`).then(res => {
        console.log(res.data.data[0]);
        // setNVInfo(res.data.data[0])
        DisPatch({ type:"NVInfoNV",payload:res.data.data[0]});
      }).catch(err => console.log(err))
    }
  }, [])
  // component did mount
 
  const HandlerDangXuat = () => {
    DisPatch({ type: "isLogin" });
    axios.get(`http://localhost:8080/Logout`).then(res => history.replace("/")).catch(err => history.replace("/"));
  }
// BUTTON DANG XUAT
const HandlerSearch = () => {
Search === undefined ||"" ? axios.get(`http://localhost:8080/KH/GetAllInfo`).then(res=>{
  DisPatch({type:"NVBHKH",payload:res.data.data})
  
}) : axios.get(`http://localhost:8080/KH/GetInfo/${Search}`).then(res => {
  DisPatch({type:"NVBHKH",payload:res.data.data})  
})

}

  return (
    <div className="NV">
      <div className="NV__L">
        <img src="https://ath2.unileverservices.com/wp-content/uploads/sites/4/2020/02/IG-annvmariv-1024x1016.jpg" alt="" className="NV__IMG" />
        <p className="NV__L--text">Xin chào, {ToDo.NVInfoNV.tennv}</p>
        <hr />
        <h5 className="NV_L--heading" onClick={() => {DisPatch({type:"NVBHSTT",payload:1});setisSearchDisable(true)}}>Quản lý thông tin cá nhân</h5>
        <h5 className="NV_L--heading" onClick={() => { DisPatch({type:"NVBHSTT",payload:2}); setisSearchDisable(false) }}>Quản lý thông tin khách hàng</h5>

        <button className="NV_L--button" onClick={HandlerDangXuat}>Đăng xuất</button>
      </div>
      <div className="NV__R">
        <div className="NV__RT">
          <i className="fas fa-home NV__Home" />
          <input type="text" className="NV__Search" name="search" placeholder="  Type what you search" disabled={isSearchDisable} onChange={(event) =>setSearch(event.target.value)} />
          <button className="NV__Button" disabled={isSearchDisable} onClick={HandlerSearch}>Search</button>
        </div>
        <div className="NV__RB">
         <NVBHData manv={manv}/>
        </div>
      </div>
    </div>
  )
}
