import React,{useEffect} from 'react'

function Item(props) {
    return props.data.map(item=>{
        return (
            <>
            <li key={item.makh}>{item.makh}</li>
            <li >{item.tenkh}</li>
            </>
        )
    })
}


export default function NVBHKHData(props) {
    useEffect(() => {
        console.log(props.data);
    })
    return (
        <ul>
            <Item data={props.data}/>
        </ul>
    )
}
