import React, { useState,useContext,useEffect } from 'react'
import axios from "axios";
import { useHistory, useParams } from 'react-router-dom';
import NVBHKHData from './NVBHKHData'
import Context from "D:/Workspace/Self-improvement/Develop/doan/src/Context"
export default function NVBHData(props) {
    const [isDisable, setisDisable] = useState(true);
    const [NVInfo, setNVInfo] = useState({});
    const [ToDo,DisPatch] = useContext(Context);
    const history = useHistory();

    useEffect(()=>{ if (ToDo.isLogin === false) 
        history.replace("/");
      
      else {
        axios.get(`http://localhost:8080/NV/GetInfoNV/${props.manv}`).then(res => {
          console.log(res.data.data[0]);
          setNVInfo(res.data.data[0])
        }).catch(err => console.log(err))
      }
    },[])
    const HandlerOnChange = (event) => {
      setNVInfo({...NVInfo,[event.target.name]: event.target.value });
    }
    const HandlerQLTNV =  () => {
        DisPatch({type:"NVInfoNV",payload:NVInfo}) ;

            axios.put(`http://localhost:8080/NV/UpdateInfoNV`, ToDo.NVInfoNV).then(res => {
            if (res.data.access === 1) {
                alert("Đã sửa đổi thông tin tài khoản thành công");
            }
            else alert("Sửa đổi thông tin thất bại");

        }).catch(err => console.log(err));
          
    }
    switch(ToDo.NVBHSTT){
      case 1: return  (
        <div className="NV__RB--items">
            <input type="text" name="tennv" id placeholder="Tên đầy đủ" disabled={isDisable} defaultValue={NVInfo.tennv} onChange={(event) => HandlerOnChange(event)} />
            <input type="password" name="matkhau" id placeholder="Mật khẩu" disabled={isDisable} defaultValue={NVInfo.matkhau} onChange={(event) => HandlerOnChange(event)} />
            <input type="datetime-local" name="ngaysinh" id placeholder="Ngày sinh" disabled={isDisable} defaultValue={NVInfo.ngaysinh} onChange={(event) => HandlerOnChange(event)} />
            <input type="number" name="cmnd" id placeholder="CMND" disabled={isDisable} defaultValue={NVInfo.cmnd} onChange={(event) => HandlerOnChange(event)} />
            <input type="tel" name="sdt" id placeholder="Số điện thoại" disabled={isDisable} defaultValue={NVInfo.sdt} onChange={(event) => HandlerOnChange(event)} />
            <input type="email" name="email" id placeholder="Email" disabled={isDisable} defaultValue={NVInfo.email} onChange={(event) => HandlerOnChange(event)} />
            <input type="text" name="diachi" id placeholder="Địa chỉ" disabled={isDisable} defaultValue={NVInfo.diachi} onChange={(event) => HandlerOnChange(event)} />
            <input type="file" name="anhnv" id placeholder="Ảnh cá nhân" disabled={isDisable} onChange={(event) => HandlerOnChange(event)} />
            <button onClick={() => setisDisable(!isDisable)}>Sửa</button>
            <button onClick={HandlerQLTNV} >Lưu</button>
       </div>
        ) 
      case 2: return <div> <NVBHKHData data={ToDo.NVBHKH}/></div>
      default: return <div>TRANG TRU</div>
    }
  }
    