import React,{useReducer} from 'react'
import { BrowserRouter as Router, Switch, Route, Link} from "react-router-dom";
import Context from "./Context";
import LogIn from "./Screen/LogIn";
import NVBH from "./Screen/NVBH";
import NVK from "./Screen/NVK";
import NVCN from "./Screen/NVCN";
import NVT from "./Screen/NVT";
import QMK from "./Screen/QMK";


const Storage = {isLogin:false,NVInfoNV:{},NVBHSTT:0,NVBHKH:[]};
const Reducer = (state,action) => {
  switch(action.type) {
    case "isLogin": return {
      ...state,
      isLogin:!state.isLogin
    }
    case "NVInfoNV":return{
      ...state,
      NVInfoNV:action.payload
    }
    case "NVBHSTT":return{
      ...state,
      NVBHSTT:action.payload
    }
    case "NVBHKH": return{
      ...state,
      NVBHKH:action.payload
    }
    default:
      throw new Error();
  }
};



export default function App() {
  const [ToDo,DisPatch] = useReducer(Reducer,Storage);


  return (
    <Context.Provider value={[ToDo,DisPatch]}>
      <Router>
         <LogIn/>
        <Switch>
          <Route path="/NVBH/:manv" component={NVBH}/>
          <Route path="/QMK" component={QMK}/>
          <Route path="/NVK/:manv" component={NVK}/>
          <Route path="/NVCN/:manv" component={NVCN}/>
          <Route path="/NVT/:manv" component={NVT}/>
        </Switch>
      </Router>
    </Context.Provider>
  )
}
