import React,{useContext,useEffect} from 'react'
import Context from '../Context';
import {useHistory} from 'react-router-dom';

export default function NVT() {
    const [ToDo,DisPatch] = useContext(Context);
    const history = useHistory();
    useEffect(()=>{
        if(ToDo.isLogin=== false){
            history.replace("/");
        }
    },[])
    return (
       <div>DAY LA TRANG NVT</div>
    )
}
