
import React,{useState,useContext} from 'react';
import axios from 'axios';
import { useHistory} from 'react-router-dom';
import Context from '../Context';
import './LogIn.css';
export default function LogIn() {
    const [ToDo,DisPatch] = useContext(Context);
    const [input,setInput] = useState({manv:"",matkhau:""});
    let history = useHistory();
    const HandlerLogIn = () => {
        // history.replace("/NVBH");
        axios.post("http://localhost:8080/Lognv",input).then(res=>{
            if(res.data.auth===1) {
                DisPatch({type:"isLogin"})
                if(res.data.access===1) {
                    history.replace(`/NVBH/${input.manv}`);
                }
                else if(res.data.access===2){
                    history.replace(`/NVK/${input.manv}`)
                }
                else if(res.data.access===3) {
                    history.replace(`/NVCN/${input.manv}`)
                }
                else if (res.data.access===4) {
                    history.replace(`/NVBT/${input.manv}`)
                };
            }
            else alert("TAI KHOAN KHONG TON TAI");
        }).catch(err=>{
            alert("KHONG THE KET NOI TOI SERVER");
        })
    }
    const HandlerQMK = () =>{
        DisPatch({type:"isLogin"});
        history.replace(`/QMK`);
    }
    const HandlerOnChange = (event)=>{
        
        setInput({...input,[event.target.name]:event.target.value});
        console.log(input);
    }
    if(ToDo.isLogin=== false) return (
        <div className="LogIn">
        <div className="LogInMain">
          <div className="LogInForm">
              <h3 className="LogInText">Quản lý thông tin</h3>
              <input name="manv" type="text" placeholder="Tài khoản" className="LogIn_InPut" onChange={(event)=>HandlerOnChange(event)}/>            
              <input name="matkhau" type="password" placeholder="Mật khẩu" className="LogIn_InPut" onChange={(event)=>HandlerOnChange(event)}/>
          
            <div className="LogInForm_Button">
              <span onClick={HandlerQMK}>Quên mật khẩu</span>
              <button className="LogIn_Button" onClick={HandlerLogIn}>Đăng nhập</button>
            </div>
          </div>
        </div>
       </div> 
      
    )
    else if (ToDo.isLogin=== true) return(<div></div>)
}
